import React, { Component } from "react";
import { NavLink } from "react-router-dom";
import { Helmet } from "react-helmet";

class HomePage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [
        {
          id: 1,
          label: "Test1",
          gifLink:
            "https://media.tenor.com/images/0a00ff974d503ba60cc67c56ffdafa7f/tenor.gif",
        },
        {
          id: 2,
          label: "Test2",
          gifLink:
            "https://media.tenor.com/images/db8149e4338a41fd24d1058053894f6a/tenor.gif",
        },
        {
          id: 3,
          label: "Test3",
          gifLink:
            "https://media.tenor.com/images/c66b5d522ff2d03c28a8586df2fbac64/tenor.gif",
        },
        {
          id: 4,
          label: "Test4",
          gifLink:
            "https://media.tenor.com/images/d450133c6a14f509424fe18dd491a90e/tenor.gif",
        },
      ],
      loadImage: {},
    };
  }
  render() {
    return (
      <div>
        <Helmet>
          <title>Gif Collections</title>
        </Helmet>
        {this.props.children}
        {this.state.data.map((item, i) => {
          return (
            <span key={i + 1}>
              <NavLink to={`/gitCollection/image/${item.label}`}>
                <img src={item.gifLink} />
              </NavLink>
            </span>
          );
        })}
      </div>
    );
  }
}

export default HomePage;
