import React, { Component } from "react";
import { Helmet } from "react-helmet";

class IndividualGif extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [
        {
          id: 1,
          label: "Test1",
          gifLink:
            "https://media.tenor.com/images/0a00ff974d503ba60cc67c56ffdafa7f/tenor.gif",
        },
        {
          id: 2,
          label: "Test2",
          gifLink:
            "https://media.tenor.com/images/db8149e4338a41fd24d1058053894f6a/tenor.gif",
        },
        {
          id: 3,
          label: "Test3",
          gifLink:
            "https://media.tenor.com/images/c66b5d522ff2d03c28a8586df2fbac64/tenor.gif",
        },
        {
          id: 4,
          label: "Test4",
          gifLink:
            "https://media.tenor.com/images/d450133c6a14f509424fe18dd491a90e/tenor.gif",
        },
      ],
      loadImage: {},
      pageUrl: "",
    };
  }

  componentDidMount() {
    console.log(this.props);

    const { history, match } = this.props;
    let pageUrl = match.url;
    let postId = match.params.label;
    let foundImage = this.state.data.find((imgObj) => imgObj.label === postId);
    console.log(foundImage);
    this.setState({
      loadImage: { ...foundImage },
      pageUrl: pageUrl,
    });
  }
  render() {
    return (
      <div>
        <Helmet>
          <meta property="og:title" content={this.state.loadImage.label} />
          <meta property="og:type" content="Gif Image" />
          <meta
            property="og:description"
            content={this.state.loadImage.label}
          />
          <meta property="og:image" content={this.state.loadImage.gifLink} />
          <meta
            property="og:url"
            content="https://niteshsinghnrs.github.io/niteshsingh/index.html"
          />
          <title>{this.state.loadImage.label}</title>
        </Helmet>
        {this.state.loadImage ? (
          <div>{<img src={this.state.loadImage.gifLink} />}</div>
        ) : null}
      </div>
    );
  }
}

export default IndividualGif;
