function App() {
  return (
    <div className="App">
      <NavLink to={"/gitCollection/"}> Home</NavLink>

      <Switch>
        <Route
          path="/gitCollection/image/:label"
          render={(routerProps) => <IndividualGif {...routerProps} />}
        ></Route>
        <Route component={HomePage}></Route>
      </Switch>

      {/* <HomePage /> */}
    </div>
  );
}

export default App;
